.section {
  margin: 0;
  padding: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
  width: map-get($breakpoints, 'mobile-sm');

  @include tablet {
    width: map-get($breakpoints, 'tablet');
    padding: 0 32px;
  }

  @include desktop {
    width: map-get($breakpoints, 'desktop');
  }
}
